@font-face {
  font-family: 'Poppins';
  src: local('Poppins Semi Bold'), local('Poppins-Semi-Bold'),
    url('./font-famaly/Poppins/Poppins-SemiBold.woff2') format('woff2'),
    url('./font-famaly/Poppins/Poppins-SemiBold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
}

